import { Injectable } from '@angular/core';
import { LogsJSON } from './logs.model';
import { FetchClient, IFetchOptions } from '@c8y/client';
import { Observable, from } from 'rxjs';
import { flatMap } from 'rxjs/operators';

interface RequestParams {
  dateFrom?;
  dateTo?;
  limit?;
}

@Injectable()
export class AppLogsService {
  private BASE_PATH_APPLICATION: string = 'application/applications';
  private header: any;

  constructor(private client: FetchClient) {
    this.header = {
      Accept: 'application/vnd.com.nsn.cumulocity.applicationLogs+json;charset=UTF-8;ver=0.9'
    };
  }

  getLogs$(appId: string, instanceName: string, params: RequestParams = {}): Observable<LogsJSON> {
    const url = `${this.BASE_PATH_APPLICATION}/${appId}/logs/${instanceName}`;
    const defaultConfig: IFetchOptions = {
      headers: this.header,
      method: 'GET'
    };
    const formattedDateFrom = params.dateFrom ? this.formatDate(params.dateFrom) : null;
    const formattedDateTo = params.dateTo ? this.formatDate(params.dateTo) : null;
    const modifiedParams = {
      ...params,
      ...(formattedDateFrom && { dateFrom: formattedDateFrom }),
      ...(formattedDateTo && { dateTo: formattedDateTo })
    };

    const config = params ? { ...defaultConfig, params: modifiedParams } : { ...defaultConfig };
    return from(this.client.fetch(url, config)).pipe(flatMap(res => res.json()));
  }

  formatDate(dateToFormat: string): string {
    return new Date(dateToFormat).toISOString();
  }
}
