import { Component, Input, Output, ViewChild, EventEmitter, ElementRef } from '@angular/core';
import { LogsJSON } from './logs.model';
import { AppLogsService } from './app-logs.service';
import { Observable, fromEvent, Subject, of, interval, NEVER, Subscription } from 'rxjs';
import {
  filter,
  catchError,
  tap,
  debounce,
  switchMap,
  takeUntil,
  finalize,
  delay,
  repeat,
  merge,
  scan
} from 'rxjs/operators';

@Component({
  selector: 'c8y-app-logs-auto-refresh',
  templateUrl: './app-logs-auto-refresh.component.html'
})
export class AppLogsAutoRefreshComponent {
  cancel$: Subject<boolean> = new Subject<boolean>();
  isAutoRefreshDisabled: boolean = false;
  logsToOutput: LogsJSON = this.getEmptyLogsJson();
  isAutoRefreshOn: boolean = true;

  @Input() selectedInstance: any;
  @Input() mo: any;
  @Input() set buttonsDisabled(areDisabled: boolean) {
    this.isAutoRefreshDisabled = areDisabled;
    if (areDisabled && this.isAutoRefreshOn) {
      this.isAutoRefreshOn = false;
      this.cancel$.next(false);
    }
  }
  @Output() onNewLogs = new EventEmitter<LogsJSON>();
  @ViewChild('autoRefresh', { static: true }) button: ElementRef;

  private subscription: Subscription;

  constructor(private appLogsService: AppLogsService) {}

  ngAfterViewInit() {
    const clicks$ = fromEvent(this.button.nativeElement, 'click').pipe(
      merge(this.cancel$),
      debounce(() => interval(300)),
      scan(this.toggleState, false),
      tap(isAutoRefreshOn => this.setButtonState(isAutoRefreshOn)),
      switchMap(isOn => (isOn ? this.watchForNewLogs() : NEVER))
    );
    this.subscription = clicks$.subscribe();
  }

  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }
  private toggleState = currentState => !currentState;

  private setButtonState(isAutoRefreshOn: boolean) {
    this.isAutoRefreshOn = isAutoRefreshOn;
  }

  private watchForNewLogs() {
    return this.startPolling().pipe(
      takeUntil(this.cancel$.pipe(filter(isAutoRefreshOn => isAutoRefreshOn === false))),
      finalize(() => {
        this.isAutoRefreshOn = false;
      })
    );
  }

  private startPolling() {
    return of(1).pipe(
      switchMap(() => this.getNewLogs().pipe(catchError(er => of(this.getEmptyLogsJson())))),
      tap(logs => this.updateLogsToOutput(logs)),
      delay(10000),
      repeat()
    );
  }

  private getNewLogs(): Observable<LogsJSON> {
    return this.appLogsService.getLogs$(this.getAppId(), this.getInstanceName());
  }

  private getAppId(): string {
    return this.mo.applicationId;
  }
  private getInstanceName(): string {
    return this.selectedInstance.name;
  }

  private updateLogsToOutput(newLogs) {
    const { dateFrom, dateTo } = newLogs;
    if (dateFrom && dateTo) {
      this.logsToOutput = { ...newLogs };
      this.onNewLogs.emit(this.logsToOutput);
    }
  }

  private getEmptyLogsJson(): LogsJSON {
    return {
      dateFrom: null,
      dateTo: null,
      logs: '',
      truncated: false
    };
  }
}
